module.exports = "// tslint:disable:no-any\nimport { HttpClient } from '@angular/common/http';\nimport { Component, OnInit } from '@angular/core';\nimport { NzMessageService } from 'ng-zorro-antd';\n\nconst fakeDataUrl = 'https://randomuser.me/api/?results=5&inc=name,gender,email,nat&noinfo';\n\n@Component({\n  selector: 'nz-demo-list-infinite-load',\n  template: `\n  <div class=\"demo-infinite-container\"\n    infiniteScroll\n    [infiniteScrollDistance]=\"2\"\n    [infiniteScrollThrottle]=\"50\"\n    (scrolled)=\"onScroll()\"\n    [scrollWindow]=\"false\">\n    <nz-list [nzDataSource]=\"data\" [nzRenderItem]=\"item\">\n      <ng-template #item let-item>\n        <nz-list-item>\n          <nz-list-item-meta\n              [nzTitle]=\"nzTitle\"\n              nzAvatar=\"https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png\"\n              [nzDescription]=\"item.email\">\n              <ng-template #nzTitle>\n                  <a href=\"https://ng.ant.design\">{{item.name.last}}</a>\n              </ng-template>\n          </nz-list-item-meta>\n        </nz-list-item>\n      </ng-template>\n      <nz-spin *ngIf=\"loading && hasMore\" class=\"demo-loading\"></nz-spin>\n    </nz-list>\n  </div>\n  `,\n  styles: [ `\n  :host ::ng-deep .demo-infinite-container {\n    border: 1px solid #e8e8e8;\n    border-radius: 4px;\n    overflow: auto;\n    padding: 8px 24px;\n    height: 300px;\n  }\n  :host ::ng-deep .demo-loading {\n    position: absolute;\n    bottom: -40px;\n    left: 50%;\n  }\n  ` ]\n})\nexport class NzDemoListInfiniteLoadComponent implements OnInit {\n  data: any[] = [];\n  loading = false;\n  hasMore = true;\n\n  constructor(private http: HttpClient, private msg: NzMessageService) {}\n\n  ngOnInit(): void {\n    this.getData((res: any) => this.data = res.results);\n  }\n\n  getData(callback: (res: any) => void): void {\n    this.http.get(fakeDataUrl).subscribe((res: any) => callback(res));\n  }\n\n  onScroll(): void {\n    if (this.loading) return;\n    this.loading = true;\n    if (this.data.length > 14) {\n      this.msg.warning('Infinite List loaded all');\n      this.hasMore = false;\n      this.loading = false;\n      return;\n    }\n    this.getData((res: any) => {\n      this.data = this.data.concat(res.results);\n      this.loading = false;\n    });\n  }\n}\n"