module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-list-grid',\n  template: `\n  <nz-list [nzDataSource]=\"data\" [nzRenderItem]=\"item\" [nzGrid]=\"{gutter: 16, span: 6}\">\n    <ng-template #item let-item>\n      <nz-list-item [nzContent]=\"nzContent\">\n        <ng-template #nzContent>\n          <nz-card [nzTitle]=\"item.title\">\n            Card content\n          </nz-card>\n        </ng-template>\n      </nz-list-item>\n    </ng-template>\n  </nz-list>\n  `\n})\nexport class NzDemoListGridComponent {\n  data = [\n    {\n      title: 'Title 1',\n    },\n    {\n      title: 'Title 2',\n    },\n    {\n      title: 'Title 3',\n    },\n    {\n      title: 'Title 4',\n    },\n  ];\n}\n"