module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-list-basic',\n  template: `\n  <div style=\"margin-bottom: 8px;\"><button nz-button (click)=\"change()\">Switch Data</button></div>\n  <nz-list [nzDataSource]=\"data\" [nzRenderItem]=\"item\" [nzItemLayout]=\"'horizontal'\" [nzLoading]=\"loading\">\n    <ng-template #item let-item>\n      <nz-list-item>\n        <nz-list-item-meta\n          [nzTitle]=\"nzTitle\"\n          nzAvatar=\"https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png\"\n          nzDescription=\"Ant Design, a design language for background applications, is refined by Ant UED Team\">\n          <ng-template #nzTitle>\n            <a href=\"https://ng.ant.design\">{{item.title}}</a>\n          </ng-template>\n        </nz-list-item-meta>\n      </nz-list-item>\n    </ng-template>\n  </nz-list>\n  `\n})\nexport class NzDemoListBasicComponent {\n  loading = false;\n  data = [\n    {\n      title: 'Ant Design Title 1'\n    },\n    {\n      title: 'Ant Design Title 2'\n    },\n    {\n      title: 'Ant Design Title 3'\n    },\n    {\n      title: 'Ant Design Title 4'\n    }\n  ];\n\n  change(): void {\n    this.loading = true;\n    if (this.data.length > 0) {\n      setTimeout(() => {\n        this.data = [];\n        this.loading = false;\n      }, 1000);\n    } else {\n      setTimeout(() => {\n        this.data = [\n          {\n            title: 'Ant Design Title 1'\n          },\n          {\n            title: 'Ant Design Title 2'\n          },\n          {\n            title: 'Ant Design Title 3'\n          },\n          {\n            title: 'Ant Design Title 4'\n          }\n        ];\n        this.loading = false;\n      }, 1000);\n    }\n  }\n}\n"